import React from "react";
import { action } from "mobx";

// @ts-ignore
import { start_read } from "./start-read.module.css";
import { stateStore } from "@aspects/stores";
import { start } from "@aspects/actions";

export type StartReadProps = {};

export const StartRead = (props: StartReadProps) => {
  const startRead = action(() => {
    stateStore.showTextarea = false;
    start();
  });

  return (
    <div onClick={startRead} className={start_read}>
      <span>התחל קריאה</span>
    </div>
  );
};
