import React, { ChangeEvent } from "react";
import { action } from "mobx";
import { observer } from "mobx-react";
import { stateStore } from "@aspects/stores";

// @ts-ignore
import { text_card_body } from "./text-card-body.module.css";
import { ReadingTextContainer } from "./components";

export type TextCardBodyProps = {};

export const TextCardBody = observer((props: TextCardBodyProps) => {
  const { showTextarea } = stateStore;

  const onTextareaChange = action((event: ChangeEvent<HTMLTextAreaElement>) => {
    stateStore.textareaText = event.target.value;
  });

  return (
    <div className={text_card_body}>
      {showTextarea ? (
        <textarea onChange={onTextareaChange} rows={4} cols={50} />
      ) : (
        <ReadingTextContainer />
      )}
    </div>
  );
});
