import React from "react";
import { observer } from "mobx-react";

import { stateStore } from "@aspects/stores";

import {
  // @ts-ignore
  reading_text_container, // @ts-ignore
  input_text, // @ts-ignore
} from "./reading-text-container.module.css";

export type ReadingTextContainerProps = {};

export const ReadingTextContainer = observer(
  (props: ReadingTextContainerProps) => {
    const { readingTextWords, highlightClassIndex } = stateStore;

    return (
      <div className={reading_text_container}>
        {readingTextWords.map((word, index) => (
          <div className={input_text} key={index}>
            <span
              style={
                highlightClassIndex === index
                  ? { backgroundColor: "yellow" }
                  : {}
              }
              id={`word-${index}`}
            >
              {word}
            </span>
            &nbsp;
          </div>
        ))}
      </div>
    );
  }
);
