import { makeAutoObservable } from "mobx";

class StateStore {
  constructor() {
    makeAutoObservable(this);
  }

  private _readingSpeed: number = 20;
  get readingSpeed(): number {
    return this._readingSpeed;
  }
  set readingSpeed(newReadingSpeed: number) {
    this._readingSpeed = newReadingSpeed;
  }

  private _textareaText: string = "";
  get textareaText(): string {
    return this._textareaText;
  }
  set textareaText(newReadingText: string) {
    this._textareaText = newReadingText;
  }

  get readingTextWords(): string[] {
    return this.textareaTextToReadingWords(this._textareaText);
  }

  private _showTextarea: boolean = true;
  get showTextarea(): boolean {
    return this._showTextarea;
  }
  set showTextarea(newShowTextarea: boolean) {
    this._showTextarea = newShowTextarea;
  }

  private _highlightClassIndex: number = -1;
  get highlightClassIndex(): number {
    return this._highlightClassIndex;
  }
  set highlightClassIndex(newHighlightClassIndex: number) {
    this._highlightClassIndex = newHighlightClassIndex;
  }

  // UTILS:
  textareaTextToReadingWords = (textareaText: string) => {
    return textareaText ? this.htmlToText(textareaText).split(" ") : [];
  };

  htmlToText = (html: string) => {
    return html.replace(/<p>/g, " <br/> ").replace(/<\/p>/g, "");
  };
}

export const stateStore = new StateStore();
