import React from "react";
import { action } from "mobx";

// @ts-ignore
import { delete_text } from "./delete-text.module.css";
import { stateStore } from "@aspects/stores";

export type DeleteTextProps = {};

export const DeleteText = (props: DeleteTextProps) => {
  const newText = action(() => {
    stateStore.showTextarea = true;
    stateStore.textareaText = "";
    
  });

  return (
    <div onClick={newText} className={delete_text}>
      <span>טקסט חדש</span>
    </div>
  );
};
