import { action } from "mobx";
import { stateStore } from "@aspects/stores";

let _interval: NodeJS.Timeout;

export const start = action(() => {
  stop();
  stateStore.highlightClassIndex = 0;
  _interval = setInterval(() => {
    console.log(stateStore.highlightClassIndex);
    if (document.querySelector(`#word-${stateStore.highlightClassIndex}`)) {
      stateStore.highlightClassIndex++;
    } else {
      stop();
    }
  }, (60 / stateStore.readingSpeed) * 1000);
});

export const stop = () => {
  clearInterval(_interval);
  stateStore.highlightClassIndex = -1;
};

export const changeFontSize = (newFontSize: number) => {
  document.querySelectorAll(".input-text").forEach(
    // @ts-ignore
    (element) => (element.style.fontSize = `${newFontSize}px`)
  );
};
